<template>
  <div class="toggler-wrapper">
    <button :class="['toggler-button', { active: isOpened }]" @click="toggle()">
      <div class="toggler-button__text">
        {{ isOpened ? "Hide" : "Show" }} issues
      </div>
      <svg
        width="10"
        height="6"
        viewBox="0 0 10 6"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
        class="toggler-button__svg toggler-button__svg--active"
      >
        <path
          d="M9.79959 5.26034C9.51774 5.56387 9.04319 5.58144 8.73966 5.29959L5 1.77348L1.26033 5.29959C0.956802 5.58144 0.482254 5.56387 0.200401 5.26034C-0.0814505 4.9568 -0.0638752 4.48225 0.239658 4.2004L4.48966 0.200402C4.77742 -0.0668049 5.22257 -0.0668049 5.51033 0.200402L9.76034 4.2004C10.0639 4.48225 10.0814 4.9568 9.79959 5.26034Z"
          fill="#212121"
        />
      </svg>
    </button>
  </div>
  <slot name="toggler-comments"></slot>
</template>

<style lang="scss" scoped>
@import "./toggler.scss";
</style>

<script>
export default {
  emits: ["onToggle"],
  name: "toggler",
  data() {
    return {
      isOpened: false,
    };
  },
  methods: {
    toggle() {
      this.isOpened = !this.isOpened;
      this.$emit("onToggle", this.isOpened);
    },
  },
};
</script>
